* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-size: 62.5%;
}

body {
  background-color: #d9ecfc;
  margin: 10rem auto;
}

.grid-container {
  width: 80%;
  grid-template-areas: "dc dc jw kw"
                       "jh pa pa kw";
  gap: 2rem;
  display: grid;
}

@media (max-width: 920px) {
  .grid-container {
    grid-template-areas: "dc dc jw"
                         "jh pa pa"
                         "kw kw kw";
    gap: 2rem;
    display: grid;
  }
}

@media (max-width: 600px) {
  .grid-container {
    grid-template-areas: "dc"
                         "jw"
                         "jh"
                         "pa"
                         "kw";
    gap: 2rem;
    display: grid;
  }
}

.grid-item {
  border-radius: 5px;
  flex-direction: column;
  gap: 2rem;
  padding: 2rem;
  display: flex;
}

.flex-item {
  align-items: center;
  gap: 2rem;
  display: flex;
}

img {
  width: 2rem;
  height: 2rem;
  border: 1px solid #add8e6;
  border-radius: 50%;
}

.flex-name {
  flex-direction: column;
  display: flex;
}

.grid-item:nth-of-type(1) {
  color: #fff;
  background-color: #8a2be2;
  background-image: url("../../../bg-pattern-quotation.97325254.svg");
  background-position: right 3rem top;
  background-repeat: no-repeat;
  grid-area: dc;
}

.grid-item:nth-of-type(2) {
  color: #fff;
  background-color: #5b5e82;
  grid-area: jw;
}

.grid-item:nth-of-type(3) {
  background-color: #f5f5f5;
  grid-area: jh;
}

.grid-item:nth-of-type(4) {
  color: #fff;
  background-color: #222237;
  grid-area: pa;
}

.grid-item:nth-of-type(5) {
  background-color: #f5f5f5;
  grid-area: kw;
}

